import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";

const InstituteDepartmentTheoryClassCard = ({ subject, openAs }) => {
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={
        openAs === "DEPARTMENT_HEAD"
          ? `/q/${params.username}/member/staff/academic/department/${params.name}?type=theory-detail`
          : `/q/${params.username}/institute/member/academic/department/detail?type=cls-detail&n=theory-detail`
      }
      state={{
        ...getQuery.state,
        subId: subject?._id,
        subjectName: subject?.subjectName,
      }}
    >
      <div
        className={style.sf_card_hover}
        style={{
          padding: "0.8rem",
          boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
          marginBottom: "1rem",
        }}
      >
        <div className={style.sf_card_contatiner}>
          <img
            src={
              subject?.subjectTeacherName?.staffProfilePhoto
                ? `${imageShowUrl}/${subject?.subjectTeacherName?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {subject?.subjectName ?? ""}
            </h6>
            <p>
              {`${subject?.subjectTeacherName?.staffFirstName ?? ""} ${
                subject?.subjectTeacherName?.staffMiddleName ?? ""
              } ${subject?.subjectTeacherName?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default InstituteDepartmentTheoryClassCard;
