import { Link, useParams } from "react-router-dom";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import { assestsFeedUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
const InstituteAcademicDepartmentStaffCard = ({ staff, openAs }) => {
  const params = useParams();
  const { t } = useTranslation();
  return (
    <Link
      to={`/q/${params.username}/staff/profile`}
      state={{
        sid: staff?._id,
        // viewAs: "DEPARTMENT_HEAD",
        viewAs: openAs ? openAs : "INSTITUTE",
      }}
    >
      <div className={style.sf_card_hover}>
        <div className={style.sf_card_contatiner}>
          <img
            src={
              staff?.staffProfilePhoto
                ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>
              {`${staff?.staffFirstName ?? ""} ${
                staff?.staffMiddleName ?? ""
              } ${staff?.staffLastName ?? ""}`}
            </h6>
            <p>
              {t("index_number")} {": "}
              {staff?.staffROLLNO ?? ""}
            </p>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default InstituteAcademicDepartmentStaffCard;
