import { useState } from "react";
import { useTranslation } from "react-i18next";
import InstituteStaff from "../../../../UserStaffMember/InstituteStaff/InstituteStaff";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import StringLength from "../../../../../Custom/Validation/StringLength";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import FormInput from "../../../../Ui/Input/FormInput";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { useAddTheoryClasses } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";

const AddInstituteDepartmentTheoryClass = ({
  clsId,
  onClose,
  onRefetch,
  instituteId,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    subjectName: "",
    staff: "",
  });
  const [staff, setStaff] = useState("");
  const [step, setStep] = useState(1);
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [addTheoryClasses] = useAddTheoryClasses();

  const onInputChange = (e, lnt) => {
    if (e.target.value?.length <= lnt) {
      setState((prev) => ({
        ...prev,
        [e.target.name]: e.target.value,
      }));
    }
  };

  const onSelectStaff = (val) => {
    setStaff(val);
    setState((prev) => ({
      ...prev,
      staff: val?._id,
    }));
    setStep(2);
  };

  const onCreate = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation)) {
      if (clsId) {
        setDisabled((pre) => !pre);
        addTheoryClasses({
          cid: clsId,
          bodyParameter: state,
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  // console.info("dsfdsg", state);

  return (
    <>
      {step === 3 ? (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={() => setStep(2)}
          instituteId={instituteId}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_theory_class")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <FormInput
                label={t("subject_name")}
                placeholder={t("subject_name_placeholder")}
                name={"subjectName"}
                value={state.subjectName}
                type={"text"}
                onChange={(e) => onInputChange(e, 45)}
                errorShow={errorField["subjectName"]}
              />
              <StringLength maxLength={45} valueLength={state.subjectName} />

              <div
                className={style.card_container_image}
                onClick={() => setStep(3)}
                style={{
                  cursor: "pointer",
                }}
              >
                <img
                  alt="not found"
                  src={
                    staff?.staffProfilePhoto
                      ? `${imageShowUrl}/${staff.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                />
                <div className={style.card_container_inner}>
                  {!staff?.staffFirstName ? (
                    <>
                      <h6>
                        {t("assign_shead")} {t("optional")}
                      </h6>
                      <p>{t("search_from_joined_list")}</p>
                    </>
                  ) : (
                    <>
                      <h6>{`${staff?.staffFirstName ?? ""} 
                    ${staff?.staffMiddleName ?? ""}
                    ${staff?.staffLastName ?? ""}`}</h6>
                      <p>
                        {t("index_number_1")} {staff?.staffROLLNO ?? ""}
                      </p>
                    </>
                  )}
                </div>
              </div>
              <CreateButton label="create" onAction={onCreate} />
            </UserMemberContentWrapper>

            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddInstituteDepartmentTheoryClass;
